.root {
  width: 100%;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  .game {
    flex-grow: 1;
    button {
      min-width: 50px;
      min-height: 50px;
    }

    .attempts {
      .attempt {
        span {
          border: 1px solid white;
          width: 50px;
          height: 50px;
          margin: 10px;
          display: inline-block;

          &.wrong {
            background: gray;
          }

          &.wrongButRight {
            background: #c5c502;
          }

          &.correct {
            background: green;
          }
        }
      }
    }
  }

  canvas {
    position: absolute;
  }
}

.App-logo {
  height: 4vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

body {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

header {
  display: flex;
  width: 100vw;;

  h1 {
    flex-grow: 1;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
